import React from 'react';
import { StatusContainer } from './StatusContainer';
import s from './wall.module.css';

const Friend = (props) => {
    return (
        <div>
            <img className={s.icon} src={props.src} />
            <span>{props.person}</span>
        </div>
    )
}

const CreateWall = (props) => {
    return (
        <Friend src={props.src} person={props.person}/>
    )
}


const Wall = (props) => {
    let Wall2 = props.profilePage.friends.map(p => <CreateWall src={p.src} person={p.person} />)
    return (
    <div className={s.gridwall}>
            <h1>Online</h1>
            <h2>{props.profilePage.mainInfo.fullName}</h2>
            <p>About me</p>
            <p>{props.profilePage.mainInfo.aboutMe}</p>
            <StatusContainer />
            <h2>My friends</h2>
            <div className={s.friends}>
                {Wall2}
            </div>
        </div>
    )
}

export default Wall;