import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { ProfileAPI } from "../../../API/Api";
import { GetStatusThunkAC, updateStatusField, PutStatusThunk } from "../../../Redux/profilePageReducer";
import { setStatus } from "../../../Redux/profilePageReducer";

const statusField = React.createRef('statusField');

const noStatus = 'No status';

export class Status extends React.Component {

    componentDidMount() {
        if (this.props.GetStatusThunkAC === undefined) {
            return null
        } else {
            this.props.GetStatusThunkAC(26279)
        }
    }

    componentDidUpdate(prevProps, prevState) {
        if (prevProps.status !== this.props.status) {
            this.setState({
                ...this.state,
                currentStatusField: this.props.status
            })
        }
    }

    state = {
        editMode: false,
        currentStatusField: this.props.status
    }

    changeCurrentStatus = (text) => {
        this.setState({
            ...this.state,
            currentStatusField: text
        })
    }

    activateEditMode = () => {
        this.setState(
            {
                ...this.state,
                editMode: true
            }
        )
    }

    deActivateEditMode = (text) => {
        this.setState({
            ...this.state,
            editMode: false
        })
        this.props.PutStatusThunk(text)
    }

    render() {
        if (this.state.editMode === false) {
            return <span
                onDoubleClick={this.activateEditMode}>
                    {(!this.props.status) ? noStatus : this.props.status}
            </span>
        } else {
            return <textarea
                autoFocus={true}
                ref={statusField}
                onBlur={() => { this.deActivateEditMode(this.state.currentStatusField) }}
                onChange={() => { this.changeCurrentStatus(statusField.current.value) }}
                value={this.state.currentStatusField}>
            </textarea>
        }
    }
}

const StatusWithHook = (props) => {
    // props.GetStatusThunkAC(26279)
    let [editMode, setEditMode] = useState(false);
    let [status, setStatus] = useState(props.status);

    let state = 15;

    useEffect( () => {
        setStatus(props.status)
    }, [props.status])

    let changeCurrentStatus = (text) => {
        setStatus(text)
    }

    let activateEditMode = () => {
        setEditMode(true)
    }

    let deActivateEditMode = (text) => {
        setEditMode(false)
        props.PutStatusThunk(text)
    }
    return <div>
        {(editMode === false) ?
            <span
                onDoubleClick={activateEditMode}>
                {(!props.status) ? noStatus : props.status}
            </span>
            : <textarea
                autoFocus={true}
                ref={statusField}
                onBlur={() => { deActivateEditMode(status) }}
                onChange={() => { changeCurrentStatus(statusField.current.value) }}
                value={status}>
            </textarea>
        }
    </div>
}

const mapStateToProps = (state) => {
    return {
        status: state.profilePage.status
    }
}

export const StatusContainer = connect(mapStateToProps, { updateStatusField, setStatus, GetStatusThunkAC, PutStatusThunk })(Status)