import React from 'react';
import p from './newcom.module.css';
import UserSmallIcon from './../../../Redux/Pictures/UserSmallIcon.png';
import { Field, reduxForm } from 'redux-form';
import { Textarea, Errors, Fields } from '../../../Validators/Validators';

const Newcom = (props) => {
    return (
        <div className={p.Newcom}>
            <div>
                <img src={(!props.profilePage.mainInfo.photos.large) ? UserSmallIcon : props.profilePage.mainInfo.photos.large}></img>
            </div>
            <div>
                <NewPost addPost={props.addPost} />
            </div>

        </div>
    )
}

const maxLength50 =  Errors.MaxLength(10);

const PostForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>
            <Field
                component={Fields.Textarea}
                name='post'
                placeholder='Enter your post'
                validate={[maxLength50, Errors.fieldRequired]}
            />
            <button>Create post</button>
        </form>
    )
}

const PostReduxForm = reduxForm({
    form: 'post'
})(PostForm);

const NewPost = (props) => {
    let onSubmit = (formData) => {
        props.addPost(formData.post)
    }
    return <PostReduxForm onSubmit={onSubmit} />
}

export default Newcom;
