import React from 'react';
import p from './oldcom.module.css';
import UserSmallIcon from './../../../Redux/Pictures/UserSmallIcon.png';

class Oldcom extends React.PureComponent {
  render() {
    console.log('rendered')
    return <div className={p.Oldcom}>
    <div>
      <img className={p.photo} src={this.props.src}></img>
      <span>2</span><button>like</button>
    </div>
    <div className={p.Textfield}>
      <span><strong>{this.props.person}</strong></span>
      <p>{this.props.text}</p>
    </div>
  </div>
  }
}

