import React from 'react';
import p from './Profile.module.css';
import Newcom from './Comments/newcom';
import Wall from './Wall/wall';
import ComMap from './Comments/oldcom';
import UserSmallIcon from './../../Redux/Pictures/UserSmallIcon.png';
import { Navigate } from 'react-router-dom';



const Profile = (props) => {
  const Input = (props) => {
    return <div>
      <input type="file" name="uploadfile" id="img" className={p.hidden} {...props} />
      <label for="img" className={p.addPhotoButton}>Add new photo</label>
    </div>
  }
  const onAddedPhoto = (event) => {
    if (event.target.files.length) {
      const file = event.target.files[0]
      props.SaveNewPhotoThunk(file)
    }
  }
  return (
    <div>
      <div className={p.profile}>
        <div className={p.Summary}>
        <img src={(!props.profilePage.mainInfo.photos.large) ? UserSmallIcon : props.profilePage.mainInfo.photos.large}></img>
          {(props.userId === props.myId) ? <Input
            large={props.profilePage.mainInfo.photos.large}
            onChange={onAddedPhoto} /> : undefined}
          <a href=''>Friends: 0</a>
          <a href=''>Notes: 0</a>
          <a href=''>Groups: 0</a>
          <a href=''>Gifts: 0</a>
        </div>
        <div><Wall profilePage={props.profilePage} /></div>
      </div>
      <div>
        <h3>Comments</h3>
        <Newcom profilePage={props.profilePage} addPost={props.addPost} updateNewPostField={props.updateNewPostField} />
        <div className={p.comments}>
          <ComMap commentsData={props.profilePage.commentsData} />
        </div>

      </div>
    </div>
  )
}

export default Profile;