import React from 'react';
import { addPost, ProfilePageThunkAC } from '../../Redux/profilePageReducer';
import Profile from './Profile';
import { connect } from 'react-redux';
import Preloader from '../Users/preloader';
import { withAuthRedirect, withRouter } from '../../hoc/hoc';
import { compose } from 'redux';
import { ProfileSelectors } from '../../Redux/Selectors';
import { createSelector } from 'reselect';
import { SaveNewPhotoThunk } from '../../Redux/profilePageReducer';

class ProfileAPIComponent extends React.Component {
  componentDidMount() {
    this.props.ProfilePageThunkAC(this.props.router)
  }
  render() {
    return <div>
      {(this.props.profilePage.isLoading === true) ? <Preloader /> : <Profile
        profilePage={this.props.profilePage}
        updateNewPostField={this.props.updateNewPostField}
        addPost={this.props.addPost}
        isAuthorized={this.props.isAuthorized}
        userId={this.props.userId}
        myId={this.props.myId}
        SaveNewPhotoThunk={this.props.SaveNewPhotoThunk}
      />}
    </div>
  }
}

let mapStateToProps = (state) => {
  return {
    profilePage: ProfileSelectors.getProfilePage(state),
    isAuthorized: ProfileSelectors.getIsAuthorized(state),
    userId: state.profilePage.mainInfo.userId,
    myId: state.authPage.data.id
  }
}

const ProfileContainer = compose(
  withRouter,
  withAuthRedirect,
  connect(mapStateToProps, { addPost, ProfilePageThunkAC, SaveNewPhotoThunk})
)(ProfileAPIComponent);


export default ProfileContainer;